import React, { Component } from 'react';
import {connect} from 'react-redux';

import CollectionContainer from './containers/CollectionContainer';
import ControlsContainer from './containers/ControlsContainer';
import SoundLibraryContainer from './containers/SoundLibraryContainer';
//import About from './components/about';
import {changeAppVar} from './actions/appActions';
import {isKeypressEnter} from './Helpers';

const sidebarStates = {MAIN: "MAIN", ABOUT: "ABOUT", LIBRARY: "LIBRARY", HOWTO: "HOWTO"}

class App extends Component {
  constructor(props){
    super(props);
    this.mediaButtonRef = this.mediaButtonRef.bind(this);
    this.changeSidebarState = this.changeSidebarState.bind(this);
      this.changeName = this.changeName.bind(this);
    this.nameInputBlur = this.nameInputBlur.bind(this);
    this.nameInputChange = this.nameInputChange.bind(this);
    this.state = {
      mediaButtons: {},
      sidebarState: sidebarStates.MAIN,
      editingName:false,
      delayTimer:null,
      name: this.props.app.name
    }
  }

  //Function for passing reference to effect media buttons up from children
  //In order to play sounds from sibling container
  mediaButtonRef(){
    //Function returns an object with 2 functions
    return {
      //Sets the reference to the play and pause buttons in the state
      setRef:(play, pause, id)=>{
        const ref = {[id]:{
          "play":play,
          "pause":pause
        }}
        this.setState(prevState =>({
          mediaButtons: Object.assign({}, prevState.mediaButtons,ref)
        }));
      },
      //Deletes the reference from the state on unload
      removeRef:(id)=>{
        this.setState(prevState =>({
          mediaButtons: delete prevState[id]
        }));
      }
    };
  }

  changeSidebarState(newVal){
    let newState = "";
    switch(newVal){
      case sidebarStates.LIBRARY:
        newState = sidebarStates.LIBRARY;
        break;
      case sidebarStates.ABOUT:
        newState = sidebarStates.ABOUT;
        break;
      default:
        newState = sidebarStates.MAIN;
    }

    this.setState({
      sidebarState : newState
    });
  }

  changeName(){
    const name = this.editNameInput.value.trim();
    if(name !== ""){
      this.props.changeAppVar("name",name);
    }
    this.setState({editingName:false})
  }
  nameInputChange(e){
    this.setState({name:e.target.value})
  }
  nameInputBlur(){
    const delayTimer = window.setTimeout(()=>{
      this.setState({editingName:false});
    },200);
    this.setState({delayTimer:delayTimer});
  }

  render() {

    const sidebar = () =>{
      return (
        <div>
          <ControlsContainer mediaButtons={this.state.mediaButtons} changeAppVar={this.props.changeAppVar} changeSidebarState={(v) => this.changeSidebarState(v)}/>
          <SoundLibraryContainer classes={this.state.sidebarState !== sidebarStates.LIBRARY ? "hidden" : ""} changeSidebarState={(v)=>this.changeSidebarState(v)}/>
        </div>
      );
    }

    const name = () =>{
      if(this.state.editingName){
        return(
          <div className="app-name-input">
            <input type="text" onKeyPress={(e)=>isKeypressEnter(e,()=>this.changeName())} ref={(input) => {this.editNameInput = input}} onChange={this.nameInputChange} value={this.state.name} onBlur={this.nameInputBlur}></input>
            <i className="fas fa-check" onClick={this.changeName} onFocus={()=>this.setState({editingName:true})}></i>
          </div>
        );
      }
      return(<h5 onClick={()=>this.setState({editingName:true})}>{this.props.app.name}</h5>);
    }

    return (
      <div className="container-fluid no-gutters">
        <div className="row no-gutters">
          <div className="col-12 col-md-3 col-lg-3 col-xl-2">
            <div className="sidebar-container">
              {sidebar()}
            </div>
          </div>
          <div className="col-12 col-md-9 col-lg-9 col-xl-10">
            <div className="soundscape-builder">
              <div className="builder-head">
                {name()}
              </div>
              <CollectionContainer/>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
// <DragDropContext onDragEnd={this.onDragEnd}>
//   <EffectContainer mediaButtonRef={this.mediaButtonRef}/>
// </DragDropContext>

const mapStateToProps = (state) =>{
  return{
    effects: state.effects,
    sounds: state.sounds,
    app: state.app
  }
}

const mapDispatchToProps = (dispatch) =>{
  return{
    changeAppVar:(varName,value)=>{
      dispatch(changeAppVar(varName,value));
    }
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(App);



